// this is the fallback for some attributes which should be set to "not-set" if missing

import { Resource } from '@opentelemetry/resources';
import { SemanticResourceAttributes } from '@opentelemetry/semantic-conventions';
import { GitResourceAttributes } from './LabelNames';

export const NOT_SET_ATTRIBUTE_VALUE = 'not-set';

// later resources will try to fetch the values from other sources, overriding this one if present
export const notSetResource = new Resource({
    [SemanticResourceAttributes.SERVICE_NAME]: NOT_SET_ATTRIBUTE_VALUE,
    [SemanticResourceAttributes.SERVICE_VERSION]: NOT_SET_ATTRIBUTE_VALUE,
    [SemanticResourceAttributes.DEPLOYMENT_ENVIRONMENT]: NOT_SET_ATTRIBUTE_VALUE,
    [GitResourceAttributes.VCS_COMMIT_ID]: NOT_SET_ATTRIBUTE_VALUE,
    [GitResourceAttributes.VCS_CLONE_ID]: NOT_SET_ATTRIBUTE_VALUE,
});
